import Button from "../../components/Button";
import { useModal } from "../../contexts/modalContext";
import ChangePassword from "./ChangePassword";

const Security = () => {
  const { openModal } = useModal();

  return (
    <div className="space-y-6">
      <Tab
        title="Change Password"
        description="Change you password to protect your account."
        button={
          <Button onClick={() => openModal(<ChangePassword />)}>
            Change Password
          </Button>
        }
      />
      <Tab
        title="Two-Factor Authentication"
        description="Add an extra layer of security to your account by enabling
            two-factor authentication."
        button={<Button>Enable 2FA</Button>}
      />
    </div>
  );
};

export default Security;

const Tab = ({ title, description, button }) => {
  return (
    <div className="bg-offWhite p-4 rounded-lg shadow space-y-4">
      <h3 className="text-lg font-medium">{title}</h3>
      <p className="text-sm text-secondary">{description}</p>
      {button}
    </div>
  );
};
