import Button from "../../components/Button";
import InputBox from "../../components/InputBox";
import useForm from "../../hooks/useForm";
import { changePasswordFields } from "../../utils/Constants";

const ChangePassword = () => {
  const onSubmitHandler = (data) => {
    console.log(data);
    clearForm();
  };

  const { formData, handleChange, handleSubmit, clearForm } = useForm(
    {},
    onSubmitHandler
  );

  return (
    <div className="size-[520px] bg-white shadow-primary border border-primary rounded-3xl p-6">
      <h2 className="capitalize text-2xl font-medium text-shadow-lg text-center my-8">
        change password
      </h2>
      <form onSubmit={handleSubmit}>
        <div className="space-y-6">
          {changePasswordFields.map((field, index) => (
            <InputBox
              key={index}
              label={field.label}
              type={field.type}
              name={field.name}
              value={formData[field.name]}
              onChange={handleChange}
              validation={field.validation}
            />
          ))}
        </div>
        <Button className="mx-auto px-6 py-2 rounded-full my-12">SAVE</Button>
      </form>
    </div>
  );
};

export default ChangePassword;
