import Button from "../../components/Button";
import { useModal } from "../../contexts/modalContext";
import DeleteAccount from "./DeleteAccount";

const AccountSettings = () => {
  const { openModal } = useModal();

  return (
    <div className="space-y-6">
      <Tab
        title="Delete Your Account"
        description="You can delete your account completely, If you want to proceed delete here."
        button={
          <Button onClick={() => openModal(<DeleteAccount />)}>
            Delete Account
          </Button>
        }
      />
      <Tab
        title="Download Account Data"
        description="Request a copy of your account data for your records."
        button={<Button>Download Data</Button>}
      />
    </div>
  );
};

export default AccountSettings;

const Tab = ({ title, description, button }) => {
  return (
    <div className="bg-offWhite p-4 rounded-lg shadow space-y-4">
      <h3 className="text-lg font-medium">{title}</h3>
      <p className="text-sm text-secondary">{description}</p>
      {button}
    </div>
  );
};
