import { motion } from "framer-motion";
import { useState } from "react";
import EmailPreferences from "./EmailPreferences";
import Security from "./Security";
import AccountSettings from "./AccountSettings";

const Settings = () => {
  const tabs = ["email preferences", "security", "account"];
  const [currentTab, setCurrentTab] = useState(tabs[0]);

  const renderSection = () => {
    switch (currentTab) {
      case "email preferences":
        return <EmailPreferences />;
      case "security":
        return <Security />;
      case "account":
        return <AccountSettings />;
      default:
        return <EmailPreferences />;
    }
  };

  return (
    <main className="w-full bg-white rounded-lg lg:shadow-md p-6 space-y-6 flex flex-wrap">
      <section className="LEFT min-w-40 flex-grow md:p-6">
        <h1 className="text-3xl font-semibold mb-6">Settings</h1>
        <ul className="space-y-4">
          {tabs.map((tab) => (
            <li
              key={tab}
              onClick={() => setCurrentTab(tab)}
              className={`py-3 px-5 cursor-pointer shadow font-medium relative transition-colors ${
                tab === currentTab ? "bg-white" : "bg-offWhite"
              }`}
            >
              <p className="text-sm capitalize">{tab}</p>
              {tab === currentTab && (
                <motion.span
                  layoutId="sideline"
                  className="absolute left-0 top-0 bottom-0 w-1 bg-primeRed"
                />
              )}
            </li>
          ))}
        </ul>
      </section>

      <section className="RIGHT md:p-6 md:min-w-80 flex-grow">
        <h2 className="text-3xl font-semibold text-gray-900 mb-6 capitalize">
          {currentTab}
        </h2>

        {renderSection()}
      </section>
    </main>
  );
};

export default Settings;
