import Button from "../../components/Button";
import { useAuth } from "../../contexts/authContext";
import { useModal } from "../../contexts/modalContext";
import { MdWarningAmber } from "react-icons/md";

const DeleteAccount = () => {
  const { closeModal } = useModal();
  const { deleteAccount } = useAuth();

  const handleDelete = () => {
    deleteAccount();
    closeModal();
  };

  return (
    <div className="w-100 bg-white rounded-xl p-8 space-y-12">
      <div className="HEAD space-y-2">
        <span className="block w-max mx-auto bg-mainRose p-4 rounded-full text-error">
          <MdWarningAmber size={32} />
        </span>
        <p className="font-medium text-center">Delete Account</p>
        <p className="text-sm font-medium text-primary text-center">
          After deleting your account, you will not be able to login anymore.
          All personal data will be deleted.
        </p>
      </div>

      <div className="BTN-SECTION flex justify-center gap-8">
        <Button onClick={handleDelete}>delete</Button>
        <Button reverse onClick={() => closeModal()}>
          cancel
        </Button>
      </div>
    </div>
  );
};

export default DeleteAccount;
