import ToggleSwitch from "../../components/ToggleSwitch";
import useForm from "../../hooks/useForm";

const EmailPreferences = () => {
  const { formData, handleChange } = useForm({ promotions: true });

  return (
    <div className="space-y-6">
      <Preference
        title="Promotions"
        description="Stay in the loop for exciting giveaways, discounts, and special partner perks."
        toggleForm={
          <form>
            <ToggleSwitch
              name="promotions"
              checked={formData.promotions}
              onChange={handleChange}
            />
          </form>
        }
      />
      <Preference
        title="Product Updates"
        description="Get the latest updates on new features and products."
        toggleForm={
          <form>
            <ToggleSwitch
              name="updates"
              checked={formData.updates}
              onChange={handleChange}
            />
          </form>
        }
      />
      <Preference
        title="Newsletters"
        description="Subscribe to our monthly newsletter."
        toggleForm={
          <form>
            <ToggleSwitch
              name="newsletter"
              checked={formData.newsletter}
              onChange={handleChange}
            />
          </form>
        }
      />
    </div>
  );
};

export default EmailPreferences;

const Preference = ({ title, description, toggleForm }) => {
  return (
    <div className="bg-offWhite p-4 rounded-lg flex justify-between items-center gap-4 shadow">
      <div>
        <h3 className="text-lg font-medium">{title}</h3>
        <p className="text-sm text-secondary">{description}</p>
      </div>
      {toggleForm}
    </div>
  );
};
